.menu {
    height: 100vh;
    min-width: 250px;
    padding: 20px;
    z-index: 1;

    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;

    background-color: #fff;

    border-right: 1px solid #cbcbcb;
}

.footer {
    margin-top: auto;
    color: #b0b0b0;
}

.footer a {
    color: #9b9b9b;
}


@media (max-width: 1150px) {
    .menu {
        width: 100%;
        position: absolute;
        flex-direction: row;
        justify-content: center;
        height: auto;
        background: 0;
        z-index: 0;
    }

    .menu .footer {
        display: none;
    }

    .menu .buttons {
        display: none;
    }
}